.chartP {
  margin: 5px 0 36px 1px;
  color: #666666;
  font-size: 13px; }

body {
  -webkit-font-smoothing: subpixel-antialiased;
  background: #FFFFFF;
  color: #333;
  font-size: 14px;
  margin: 0;
  padding: 0;
  border: 0;
  height: 100%; }

html {
  height: 100%; }

.mainview .overviewHeader {
  background: #f7f7f7;
  color: #000;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center; }
  .mainview .overviewHeader h3 {
    font-size: 16px;
    font-family: "Muli", sans-serif;
    font-weight: 600; }

.mainview .sideview {
  min-height: 46vh;
  margin-left: 2%;
  float: left;
  letter-spacing: 0;
  width: 15vw;
  background: none !important;
  box-shadow: -12px 0 20px -12px rgba(0, 0, 0, 0.1) inset; }
  .mainview .sideview p {
    font-family: "Muli", sans-serif;
    font-size: 12px; }
  .mainview .sideview h5 {
    font-family: "AvenirNext-Medium-Mint", "Helvetica Neue", Helvetica, Arial, sans-serif;
    border-bottom: 4px solid #ccc;
    font-size: 18px;
    font-weight: 600; }
  .mainview .sideview h6 {
    font-size: 16px;
    font-family: "Muli", sans-serif;
    font-weight: 600; }

.mainview .tableBox {
  margin-top: 1.5%;
  margin-bottom: 1%;
  padding: 1%;
  border-top: 1px solid #ccc;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap; }
  .mainview .tableBox .listContainer {
    margin-bottom: 10%; }

.mainview .charts {
  justify-content: space-around;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-right: 10%; }
