@import "../../App.scss";

.register-cont {
 
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
 
  // padding-right: 20%;
  // padding-left: 30%;
  // height: 50vh;
  
  section {
    // margin-bottom: 20%;
    
    margin-top: 5%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    align-content: center;
    border: 1px solid black;
    height: 52vh;
    width: 20vw;
    padding: 5%;
    margin-bottom: 5%;
    .registerBtn {
      color: white;
      background: #0077c5;
      border: #0077c5;
      border-radius: 4px;
      float: left;
      text-transform: capitalize;
      
      width: 100%;
      line-height: 2.4rem;
      white-space: nowrap;
      cursor: pointer;
      font-size: 0.8rem;
      
    }
    
  }
  .register-header {
    text-align: center;
    padding-bottom: 20px;
    
  }
  h2 {
    color: #4c4c4c;
    line-height: 1.1;
    font-size: 24px;

    font-weight: 400;
  }
  h4 {
    font-family: $font-family;
  }
  input {
    -webkit-writing-mode: horizontal-tb !important;
    text-rendering: auto;
    color: initial;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    display: flex;
    justify-content: center;
    text-align: start;
    -webkit-appearance: textfield;
    background-color: white;
    -webkit-rtl-ordering: logical;
    cursor: text;
    margin: 0em;
    font: 400 13.3333px Arial;
    padding: 1px 20px;
    border-width: 2px;
    border-style: inset;
    height: 15%;
    width: 100%;
    
  }

  
  // .button2 {
  //   @extend .global-button;
  //   color: green;
  // }
  
  .bad-login {
  
      text-align: center;
      
      // border: 1px outset rgb(146, 134, 134);
      // border-radius: 5px;
      background:lightgray;
      color: red;
    
  }
}
