@import "../../../App.scss";
.listContainer{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  align-content: center;
}
table {
  // margin-left: 5%;
  width: 60vw;
  // display: flex;
  // flex-wrap: wrap;
  // flex-flow: column;

  flex: 1;
  // border: 1px solid #b1b1b1;
  border-collapse: collapse;
  font-size: 0.8em;
  // border: 1px solid #ddd;
  // border-collapse: separate;
  // width: 650px;
  // border-spacing: 2px;
  // font-size: 12px;
  // margin-left: 0;
  // vertical-align: middle;
  // text-align: left;

  .tfoot-cont {
    display: flex;
    height: 5vh;
    padding: 1%;
    flex-wrap: wrap;
  }
  thead {
    // margin-left: 5%;
    // margin-right: 1%;
    // display: flex;
    // background-color: #cec8c8;
    // border-bottom: 1px solid #b1b1b1;

    // vertical-align: middle;
    display: flex;
    flex-wrap: wrap;
    vertical-align: middle;
    border-color: inherit;
    border-bottom: 1px solid #ddd;
    color: #666;
    background: -webkit-linear-gradient(top, #fff, #e8e8e8);
    height: 31px;
    line-height: 28px;
    border: 1px solid #ddd;
    border-collapse: separate;

    font-size: 12px;

    // padding: 2px 4px;

    tr {
      display: flex;
      width: 100%;
      -webkit-box-align: stretch;
      align-items: stretch;
      display: flex;
      width: 100%;
      -webkit-box-align: stretch;
      align-items: stretch;
      height: inherit;
      padding: 2px !important;
      // padding: 8px;
      -webkit-border-horizontal-spacing: 1.6px;
      -webkit-border-vertical-spacing: 1.6px;

      th {
        display: table-cell;
        vertical-align: inherit;
        font-weight: bold;
        -webkit-box-flex: 1;
        flex: 1;
        min-width: 120px;
        text-align: left;
      }
    }
  }
}

form {
  
  // margin-left: 5%;
 
  flex: 1 1;
  border-collapse: collapse;
  font-size: 0.8em;
  // width: 60vw;
  padding: 1%;
  display: flex;
  flex-wrap: wrap;
  text-align: right;
  justify-content: space-between;
  .tf-category{
   
  }
  
    button {
      background-color: $bgcolor;
      color: #fff;
      margin-left: 5px;
      text-transform: uppercase;
      text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
      border-radius: 4px;
      padding: 0.5em 1em;
      text-decoration: none;
      border: transparent;
      cursor: pointer;
      height: 35px;
    }
    input {
      -webkit-box-flex: 1;
      flex: 1;
      min-width: 50px;
      height: 100%;
      font-size: 1.1em;
      padding: 0.5em 0.6em;
      display: inline-block;
      border: 1px solid #ccc;
      box-shadow: inset 0 1px 3px rgba(50, 50, 50, 0.16);
      border-radius: 4px;
      vertical-align: middle;
      box-sizing: border-box;
    }
    button {
      color: #fff;
      background: #45cae6;
      padding: 7px 8px 6px 5px;
      min-width: 65px;
      font-size: 11px;
      border-radius: 3px;
      text-transform: uppercase;
      text-align: center;
      cursor: pointer;
    }
    select {
      -webkit-box-flex: 1;
      flex: 1;
      min-width: 50px;
      height: 100%;
      font-size: 1.1em;
      padding: 0.5em 0.6em;
      display: inline-block;
      border: 1px solid #ccc;
      box-shadow: inset 0 1px 3px rgba(50, 50, 50, 0.16);
      border-radius: 4px;
      vertical-align: middle;
      box-sizing: border-box;
    }
   
  
}
